.form-container {
    text-align: center;
}

.username-form {
    margin: 40px 0;
}

.username-form::after {
    content: '';
    clear: both;
    display: table;
}

input[type=text] {
    float: right;
    width: 80%;
    padding: 6px 8px;
    border: 1px solid #d1d5da;
    border-radius: 3px;
    float: left;
}

input[type=text]:focus {
    outline: none;
}

input[type=submit] {
    float: right;
    height: 38px;
    width: 20%;
    color: #fff;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#34d058), color-stop(90%, #28a745));
    background-image: -webkit-linear-gradient(top, #34d058 0%, #28a745 90%);
    background-image: -o-linear-gradient(top, #34d058 0%, #28a745 90%);
    background-image: linear-gradient(-180deg, #34d058 0%, #28a745 90%);
    cursor: pointer;
}

.error {
    background-color: #f5e7ea;
    border:1px solid #c3002f;
    margin: 100px auto;
    min-height: 90px;
    padding: 30px 50px;
    width: 80%;
}


